import React, { useRef, useEffect } from "react";
import { Link, useStaticQuery, graphql } from "gatsby";
import animations from "../../lib/animations.lib";
import logo from "../../images/logo.svg";
import "./navbar.styles.scss";

const animate = animations();

const Navbar = () => {
  const { site } = useStaticQuery(query);
  const links = site.siteMetadata?.navbar.links;
  const navRef = useRef(null);

  // const [deviceType, setDeviceType] = useState('');

  // useEffect(() => {
  //   animate.animateNavbar(navRef.current);
  //   animate.toggleNavbar(navRef.current);
  // });

  useEffect(() => {
    let hasTouchScreen = false;
    if ("maxTouchPoints" in navigator) {
      hasTouchScreen = navigator.maxTouchPoints > 0;
    } else if ("msMaxTouchPoints" in navigator) {
      hasTouchScreen = navigator.msMaxTouchPoints > 0;
    } else {
      const mQ = window.matchMedia && matchMedia("(pointer:coarse)");
      if (mQ && mQ.media === "(pointer:coarse)") {
        hasTouchScreen = !!mQ.matches;
      } else if ("orientation" in window) {
        hasTouchScreen = true; // deprecated, but good fallback
      } else {
        // Only as a last resort, fall back to user agent sniffing
        var UA = navigator.userAgent;
        hasTouchScreen =
          /\b(BlackBerry|webOS|iPhone|IEMobile)\b/i.test(UA) ||
          /\b(Android|Windows Phone|iPad|iPod)\b/i.test(UA);
      }
    }
    if (hasTouchScreen) {
      // setDeviceType("Mobile");
      // console.log('mobile');
      animate.toggleNavbar(navRef.current);
      animate.animateNavbar(navRef.current, false);
    } else {
      // setDeviceType("Desktop");
      // console.log('desktop');
      animate.animateNavbar(navRef.current);
    }
  }, []);

  return (
    <nav
      ref={navRef}
      className="navbar is-transparent is-uppercase is-fixed-top"
      aria-label="main navigation"
    >
      <div className="container">
        <div className="navbar-brand">
          <Link className="navbar-item p-3" to="/">
            <img
              className="image"
              src={`${logo}`}
              alt="logo"
              style={{ width: "auto", height: "8vh" }}
            />
            {/* {site.siteMetadata?.title || `La Fucina dei Talenti`} */}
          </Link>
          <button
            className="navbar-burger"
            aria-label="menu"
            aria-expanded="false"
            data-target="navbar"
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </button>
        </div>

        <div id="navbar" className="navbar-menu has-text-right-mobile">
          <div className="navbar-end">
            {links &&
              links.map((link, i) => (
                <Link
                  key={i}
                  to={link.to}
                  className="navbar-item has-text-capitalized"
                >
                  {link.name}
                </Link>
              ))}
          </div>
        </div>
      </div>
    </nav>
  );
};

export default Navbar;

const query = graphql`
  query NavbarData {
    site {
      siteMetadata {
        title
        navbar {
          links {
            name
            to
          }
        }
      }
    }
  }
`;
